import Observer from './observer';
import actionTypes from './store-constants';

/*
const tagContainer: Object = {
  editorId?: number,
  id: string,
  tagName: string,
  tag: HTMLElement,
  data?: Object
}
*/


class Store extends Observer {

  constructor() {
    super();

    /* here the global space for storing objects and data */
    this._data = {};
    this._data.tags = [];
  }

  _checkTagExists(tagId) {
    let tagExists = false;
    this._data.tags.forEach((item, i) => {

      if (item.id === tagId) {
        tagExists = { item, index: i };
      }

    });

    return tagExists;
  }

  addTag(tagContainer) {

    if (this._data.tags.length === 0 || !this._checkTagExists(tagContainer.id)) {

      if (undefined === tagContainer.data.userSelectedNodeItems) {
        tagContainer.data.userSelectedNodeItems = [];
      }

      this._data.tags.push(tagContainer);

      store.trigger(actionTypes.editor.CHANGE, {
        action: {
          id: actionTypes.editor.id,
          type: actionTypes.editor.ADD_TAG,
          data: {
            tagContainer,
          },
        },

      });

    } else {
      throw new Error('Tag already exists! Will not add it.');
    }

  }

  deleteTag(tagContainer) {

    let tagExists = this._checkTagExists(tagContainer.id);

    if (tagExists !== false) {

      this._data.tags.splice(tagExists.index, 1);

      store.trigger(actionTypes.editor.CHANGE, {
        action: {
          id: actionTypes.editor.id,
          type: actionTypes.editor.DELETE_TAG,
          data: {
            tagContainer: {
              editorId: tagContainer.editorId,
              id: tagContainer.id,
              tagName: tagContainer.tagName,
              data: {},
            },
          },
        },

      });

      return true;
    }

    throw new Error('Tag does not exist! Will not delete it.');
  }

  getAllTags(opts) {


    if (undefined === opts || !opts.editorId) { return this._data.tags; }

    const selectedTags = this._data.tags.filter((item) => item.editorId === opts.editorId);

    return selectedTags;
  }

  getTagsBy(opts) {

    const allowedKeys = ['editorId', 'id', 'tagName'];

    if (opts === undefined) { return false; }
    if (allowedKeys.indexOf(opts.key) === -1) { return false; }

    if (!Array.isArray(opts.value)) {
      opts.value = [opts.value];
    }

    // console.log(opts.value);
    // console.log(this._data.tags);

    const selectedTags = this._data.tags.filter((item) => (item[opts.key] === opts.value[0]) || (item[opts.key] === opts.value[1]));

    return selectedTags;
  }

  getLastTag(opts) {

    if (undefined === opts || !opts.editorId) {
      return this._data.tags[this._data.tags.length - 1];
    }

    const selectedTags = this._data.tags.filter((item) => item.editorId === opts.editorId);

    return selectedTags[selectedTags.length - 1];
  }
}

const store = new Store();
export default store;
